import InputForm from "../form/InputForm";

export default function HomeHero() {
    return(
        <div className="px-45 py-30 max-w mx-auto bg-gradient-to-b from-white to-blue-50/20">
            <h1 className="leading-[1.1] text-center text-5xl font-bold text-blue-600">Your trusted way</h1>
            <h1 className="leading-[1.1] text-center text-5xl font-bold text-blue-600">to get things done in <span className="bg-gradient-to-r from-blue-600 to-blue-400 bg-clip-text text-transparent">India</span>.</h1>

            <p className="mt-8 text-center text-gray-600">Trusto takes care of your paperwork and government tasks on</p>
            <p className="text-center text-gray-600">your behalf — safely, legally, and reliably.</p>

            <button className="mt-8 text-white bg-blue-600 hover:bg-blue-700 px-6 py-3 rounded-lg font-medium mx-auto block transition-all shadow-sm hover:shadow hover:cursor-pointer"><a href="#">Sign in - It's free</a></button>

            <InputForm/>
        </div>
    );
};